import { h } from 'preact'
/* eslint-env browser */
import { Component, render } from 'preact'
import CommentForm from './Form.jsx'
// import { test } from '/update.js'
import LinkedIn from './LinkedIn.jsx'
import GitHub from './GitHub/index.jsx'
import User from './User.jsx'
import callbackFetch from './../fetch.js'

class App extends Component {
  constructor() {
    super()

    this.state = {
      loading: true,
      error: null,
      /** @type {!Auth} */
      auth: {},
    }
    this.pml = /** @type {function(!Event)} */(this.postMessageListener.bind(this))

    window.addEventListener('message', this.pml, false)
  }
  componentDidMount() {
    this.auth()
  }
  auth() {
    this.setState({ loading: true })
    callbackFetch(`${this.props.host}/auth`, (error, res) => {
      this.setState({ loading: false })
      if (error) {
        return this.setState({ error })
      }
      const auth = res.json()
      this.setState({ auth })
    }, {
      credentials: 'include',
    })
  }
  /**
   * @param {!MessageEvent} event
   */
  postMessageListener(event) {
    const { data, origin } = event
    if (origin != this.props.host) return
    if (data == 'signedin') this.auth()
  }
  componentWillUnmount() {
    window.removeEventListener('message', this.pml)
  }
  render() {
    return (         h('div',{},
      h(AppUser,{error:this.state.error,loading:this.state.loading,auth:this.state.auth,host:this.props.host,onSignOut:() => {
        this.setState({ auth: {} })
      }}),

      h(CommentForm,{path:`${this.props.host}/comment`,auth:this.state.auth}),

    ))
  }
}

/**
 * @param {Object} props
 * @param {Auth} props.auth
 */
const AppUser = ({ error, loading, auth, onSignOut, host }) => {
  if (error)
    return (h('div',{},`Error: `,error))
  if (loading)
    return (h('div',{},`Loading...`))
  // if (!auth.user)
  const loggedIn = auth.linkedin_user || auth.github_user
  return (h('div',{},
    !loggedIn && h('span',{},`To display the profile image and validate your GitHub profile, sign in. No email permissions are required other than default ones (no email). `,h('a',{'href':"https://www.technation.sucks/privacy-policy.html"},`Privacy`)),

    h(User,{auth:auth,onSignout:onSignOut,host:host}),

    !auth.linkedin_user && h(LinkedIn,{host:host}),
    !auth.linkedin_user && ' ',
    !auth.github_user && h(GitHub,{host:host}),

  ))
}

